import React, { Component } from "react";
import Grid from "@material-ui/core/Grid";
import Link from "@material-ui/core/Link";
import { MuiThemeProvider } from "@material-ui/core/styles";
import ReactDataSheet from "react-datasheet";
import "react-datasheet/lib/react-datasheet.css";
import "./App.css";
import getReactDataSheetDataFromData from "./utils/getReactDataSheetDataFromData";
import { CHART_TYPES, DEFAULT_DATA, MUI_THEME } from "./utils/constants";
import ChartTypeSelector from "./ChartTypeSelector";
import ChartDisplay from "./ChartDisplay";
import DownloadButton from "./DownloadButton";
import ShareButton from "./ShareButton";
import ChartOptionsToggle from "./ChartOptionsToggle";
import ChartOptions from "./ChartOptions";

const VERSION = 1;

class App extends Component {
  state = {
    selectedChartType:
      this.props.initialState.selectedChartType || CHART_TYPES[0],
    data: this.props.initialState.data || DEFAULT_DATA,
    options: this.props.initialState.options || {},
    optionsOpen: this.props.initialState.optionsOpen || false,
    redraw: false
  };

  setStateAndUrl(nextState) {
    this.setState(nextState);
    const serializable = { version: VERSION, ...this.state, ...nextState };
    delete serializable.redraw;
    window.location.hash = encodeURIComponent(JSON.stringify(serializable));
  }

  render() {
    const chartTitleText = this.state.data[0][0];
    return (
      <div id="App">
        <Grid container spacing={24}>
          <MuiThemeProvider theme={MUI_THEME}>
            <Grid item xs={12} md={6}>
              <div className="save-buttons">
                <DownloadButton chartTitleText={chartTitleText} />
                <ShareButton />
              </div>
              <ChartDisplay
                redraw={this.state.redraw}
                data={this.state.data}
                selectedChartType={this.state.selectedChartType}
                chartTitleText={chartTitleText}
                options={this.state.options}
              />
              <ChartTypeSelector
                chartType={this.state.selectedChartType}
                onChartTypeChange={selectedChartType => {
                  this.setStateAndUrl({ selectedChartType, redraw: true });
                }}
              />
              <ChartOptionsToggle
                optionsOpen={this.state.optionsOpen}
                onOptionsOpenChange={optionsOpen =>
                  this.setStateAndUrl({ optionsOpen, redraw: false })
                }
              />
              {this.state.optionsOpen && (
                <ChartOptions
                  options={this.state.options}
                  onOptionsChange={options =>
                    this.setStateAndUrl({ options, redraw: true })
                  }
                />
              )}
            </Grid>
          </MuiThemeProvider>
          <Grid item xs={12} md={6}>
            <ReactDataSheet
              data={getReactDataSheetDataFromData(this.state.data)}
              valueRenderer={cell => cell.value}
              onCellsChanged={changes => {
                const data = this.state.data.map(row => [...row]);
                changes.forEach(({ cell, row, col, value }) => {
                  data[row][col] = value;
                });
                this.setStateAndUrl({ data, redraw: false });
              }}
            />
          </Grid>
        </Grid>
        <div className="feedback-link-wrapper">
          <Link href="mailto:feedback@freechartonline.com" target="_blank">
            feedback@freechartonline.com
          </Link>
        </div>
      </div>
    );
  }
}

export default App;
