import React from "react";
import Grid from "@material-ui/core/Grid";
import FormControl from "@material-ui/core/FormControl";
import Select from "@material-ui/core/Select";
import startCase from "lodash.startcase";
import CURRENCY_CODES from "./utils/currencyCodes";

const OPTIONS_CONFIG = [
  {
    type: "select",
    key: "formatAsMoney",
    options: ["", ...CURRENCY_CODES]
  }
];

function ChartOptions({ options, onOptionsChange }) {
  return (
    <div>
      <hr />
      {OPTIONS_CONFIG.map(optionConfig => (
        <Grid container spacing={24} key={optionConfig.key}>
          <Grid item xs={6} className="chart-option-label">
            {startCase(optionConfig.key)}
          </Grid>
          <Grid item xs={6} className="chart-option-input">
            <ChartOption
              optionConfig={optionConfig}
              value={options[optionConfig.key]}
              onValueChange={value =>
                onOptionsChange({ ...options, [optionConfig.key]: value })
              }
            />
          </Grid>
        </Grid>
      ))}
    </div>
  );
}

function ChartOption({ optionConfig, value, onValueChange }) {
  switch (optionConfig.type) {
    case "select":
      return (
        <FormControl>
          <Select
            native
            value={value}
            onChange={e => onValueChange(e.target.value)}
            inputProps={{
              name: optionConfig.key,
              id: optionConfig.key
            }}
          >
            {optionConfig.options.map(option => (
              <option key={option} value={option}>
                {option}
              </option>
            ))}
          </Select>
        </FormControl>
      );
    default:
      return null;
  }
}

export default ChartOptions;
