export default function getReactDataSheetDataFromData(data) {
  const [firstRow, ...rows] = data;
  const rdsFirstRow = firstRow.map((value, j) => ({
    value: value == null ? null : value,
    className: j === 0 ? "rds-chart-name read-only" : "read-only"
  }));
  const rdsRows = rows.map((row, i) =>
    row.map((value, j) => ({
      value: value == null ? null : value,
      className: j === 0 ? "read-only" : ""
    }))
  );
  return [rdsFirstRow, ...rdsRows];
}
