// NOTE: different charts
// - bubble (multiple sources, specific data format: {x, y, r})
// - scatter (multiple sources, specific data format: {x, y})
// - bar, horizontalBar, line, radar (multiple sources)
// - polarArea, pie, doughnut (one source or multiple source)
export const CHART_TYPES = [
  "bar",
  // 'bubble', // format {x, y, r}
  // 'doughnut', // legend on labels
  "horizontalBar",
  "line",
  // 'pie', // legend on labels
  // 'polarArea', // legend on labels
  "radar"
  // 'scatter', // format {x, y}
];

const DEFAULT_COLS = 4;
const DEFAULT_ROWS = 42;
const DEFAULT_REAL_DATA = [
  ["Charts Created Last Week", "Alice", "Bob", "Carol"],
  ["Monday", 43, 70, 67],
  ["Tuesday", 88, 65, 35],
  ["Wednesday", 73, 10, 66],
  ["Thursday", 14, 59, 85],
  ["Friday", 50, 78, 66],
  ["Saturday", 59, 92, 0],
  ["Sunday", 83, 27, 27]
];
const DEFAULT_PADDING_DATA = times(
  DEFAULT_ROWS - DEFAULT_REAL_DATA.length,
  () => times(DEFAULT_COLS, () => null)
);

export const DEFAULT_DATA = DEFAULT_REAL_DATA.concat(DEFAULT_PADDING_DATA);

export const MUI_THEME = createMuiTheme({
  palette: {
    primary: {
      main: "#36a2eb",
      contrastText: '#ffffff'
    },
    secondary: {
      main: "#ff6384",
      contrastText: '#ffffff'
    }
  }
});
