import React, { Component } from "react";
import Chart from "chart.js";
import ChartDataLabels from "chartjs-plugin-datalabels";
import ChartComponent from "react-chartjs-2";
import getChartDataFromData from "./utils/getChartDataFromData";
import registerLinearScale from "./scale.linear";
import set from "lodash.set";

// register registerLinearScale
registerLinearScale(Chart);

function mergeUserOptions(chartOptions, chartType, advancedOptions) {
  if (advancedOptions.formatAsMoney) {
    const moneyFormatter = new Intl.NumberFormat([], {
      style: "currency",
      currency: advancedOptions.formatAsMoney,
      minimumFractionDigits: 0
    });
    const formatter = value => moneyFormatter.format(value);
    if (chartType === "radar") {
      set(chartOptions, "scale.ticks.callback", formatter);
    } else {
      if (chartType === "horizontalBar") {
        set(chartOptions, "scales.xAxes.0.ticks.callback", formatter);
      } else {
        set(chartOptions, "scales.yAxes.0.ticks.callback", formatter);
      }
      set(chartOptions, "plugins.datalabels.formatter", formatter);
    }
  }

  return chartOptions;
}

function getChartOptions({ chartTitleText, chartType, options }) {
  const chartOptions = {
    elements: {
      line: {
        tension: 0
      }
    },
    plugins: {
      datalabels: {
        display: chartType !== "radar",
        align: "end",
        anchor: "end"
      }
    }
  };

  if (chartTitleText) {
    chartOptions.title = {
      display: true,
      text: chartTitleText,
      position: "bottom"
    };
  }

  if (chartType === "radar") {
    chartOptions.scale = { ticks: { beginAtZero: true } };
  } else {
    chartOptions.scales = {
      xAxes: [
        {
          ticks: {
            beginAtZero: true,
            autoSkip: false
          }
        }
      ],
      yAxes: [
        {
          ticks: {
            beginAtZero: true,
            autoSkip: false
          }
        }
      ]
    };
  }

  return mergeUserOptions(chartOptions, chartType, options);
}

class ChartDisplay extends Component {
  componentDidMount() {
    Chart.pluginService.register(ChartDataLabels);
  }

  render() {
    const {
      redraw,
      data,
      selectedChartType,
      chartTitleText,
      options
    } = this.props;
    return (
      <ChartComponent
        id="chart"
        redraw={redraw}
        data={getChartDataFromData(data, selectedChartType)}
        type={selectedChartType}
        options={getChartOptions({
          chartTitleText,
          chartType: selectedChartType,
          options
        })}
      />
    );
  }
}

export default ChartDisplay;
