import React from "react";
import Switch from "@material-ui/core/Switch";

function ChartOptionsToggle({ optionsOpen, onOptionsOpenChange }) {
  return (
    <div className="options-toggle-wrapper">
      <span>
        ADVANCED OPTIONS
      </span>
      <Switch
        checked={optionsOpen}
        onChange={() => onOptionsOpenChange(!optionsOpen)}
        color="primary"
      />
    </div>
  );
}

export default ChartOptionsToggle;
