import unzip from "lodash.unzip";

const COLOR_SOLID = [
  "rgba(54,162,235)", // blue
  "rgba(255,206,86)", // yellow
  "rgba(255,99,132)" // red
];

const COLOR_TRANSPARENT = [
  "rgba(54,162,235, 0.5)", // blue
  "rgba(255,206,86, 0.5)", // yellow
  "rgba(255,99,132, 0.5)" // red
];

export default function getChartDataFromData(
  [[chartName, ...seriesNames], ...rawRows],
  chartType
) {
  const validRows = rawRows.filter(rawRow => rawRow[0]);
  if (validRows.length === 0) {
    return {
      labels: [],
      datasets: []
    };
  }

  const [labels, ...rawDatasets] = unzip(validRows);
  const datasets = rawDatasets
    .map((rawDataSet, index) => {
      const dataset = {
        label: seriesNames[index],
        data: rawDataSet
      };
      switch (chartType) {
        case "bar":
        case "horizontalBar":
          dataset.borderColor = COLOR_SOLID[index];
          dataset.backgroundColor = COLOR_SOLID[index];
          break;
        case "line":
          dataset.borderColor = COLOR_SOLID[index];
          dataset.backgroundColor = COLOR_SOLID[index];
          dataset.fill = false;
          break;
        case "radar":
          dataset.borderColor = COLOR_SOLID[index];
          dataset.backgroundColor = COLOR_TRANSPARENT[index];
          break;
        default:
          break;
      }
      return dataset;
    })
    // we do not show data on chart if there is no series name
    .filter(({ label }) => label);

  return {
    labels,
    datasets
  };
}
